.header-main
  &__text-box
    display: flex
    flex-direction: column
    gap: 24px
  &__desc
    margin-top: 153px
    text-transform: uppercase
    font-weight: 500
    font-size: 24px
    line-height: 1.33
    letter-spacing: 0.06em
    color: $white
  &__title
    max-width: 1200px
    font-weight: 700
    font-size: 64px
    line-height: 1.38
    letter-spacing: 0.035em
    color: $white
  &__wrp
    display: flex
    align-items: center
    justify-content: space-between
    margin-top: 40px

