@media ( max-width:992px )
  .header
    &__img
      height: 650px
    &__nav
      margin-left: 15px
  .header-main
    &__desc
      margin-top: 100px
    &__title
      font-weight: 700
      font-size: 48px
  .timetable
    &__container
      margin-top: 145px
  .timetable-right
    &__wrp
      margin-bottom: 50px
    &__square
      width: calc(441px - 30%)
      height: calc(502px - 20%)
    &__img1
      max-height: calc(486px - 20%)
      max-width: calc(323px - 20%)
    &__img2
      max-height: calc(486px - 20%)
      max-width: calc(323px - 20%)
      left: 275px

  .rewiews
    min-height: 483px

  .testimonials
    &__title
      font-weight: 700
      font-size: 28px
      line-height: 2.2
      margin-bottom: 10px
    &__underline
      border: 1px solid $brown
      top: 45px
      z-index: -1
    & img
      max-width: 60%

  .gallery
    height: 236px
    &__container
      padding-top: 24px
    &__img
      & img
        max-height: 326px

  .contacts
    &__button
      margin-left: 180px
      height: 80px
    & span
      padding-top: 28px
