.gallery
  max-width: 1920px
  height: 605px
  margin: 0 auto
  background: $blue
  margin-bottom: 404px
  position: relative
  &__container
    overflow: hidden
    display: flex
    justify-content: space-between
    padding-top: 68px
  &__box-text
    max-width: 569px
    min-height: 64px
  &__title
    font-weight: 700
    font-size: 48px
    line-height: 1.83
    color: $white
  &__desc
    line-height: 2
    color: $white
    margin-bottom: 50px
  &__box-btn
    display: flex
    align-items: center
    gap: 82px
  &__img
    display: flex
    max-width: 100%
    position: absolute
    gap: 30px
    & img
      max-height: 646px
      min-width: 30%
      object-fit: cover

.arrow-left
  background: $blue
  transition: all 0.2s ease-in
  &:hover
    transform: scale(0.9)

.arrow-right
  background: $blue
  rotate: 180deg
  transition: all 0.2s ease-in
  &:hover
    transform: scale(0.9)
