.header
  position: relative
  &__wrapper
    padding-top: 56px
    display: flex
    justify-content: space-between
    align-items: center
  &__nav
    display: flex
    align-items: center
    height: 55px
    margin-left: 130px
  &__call-menu
    display: flex
    align-items: center
    gap: 11px
    & img
      height: 24px
      width: 24px
    & a
      color: $white
      transition: all 0.3 ease-in
      &:hover
        text-decoration: underline
        text-decoration-color: $brown
  &__img
    position: absolute
    object-fit: cover
    width: 100%
    height: 901px
    z-index: -1
    top: 0
    right: 0
    filter: brightness(51%)

.nav
  &__list
    display: flex
    align-items: center
    gap: 45px
  &__item
    color: $white
  &__link
    transition: all 0.3 ease-in
    &:hover
      text-decoration: underline
      text-decoration-color: $brown
