.social
  display: flex
  color: $white
  &__circle
    position: relative
    display: flex
    justify-content: center
    align-items: center
    width: 48px
    height: 48px
    border: 1px solid $white
    border-radius: 100px
    margin-right: 25px
    transition: all 0.1s ease-in
    &:last-child
      margin-right: 0px
    &:hover
      background: $blue
      transform: scale(1.1)
      border: 1px solid $blue
    & a:before
        content: ''
        position: absolute
        top: 0
        left: 0
        width: 100%
        height: 100%




