@media ( max-width:768px )
  body
    &._lock
      overflow: hidden
  .header
    & a
      height: 25px
      width: 186px
    &__img
      height: 474px

  .nav
    padding-left: 30px
    margin: 0
    position: fixed
    top: 0
    left: 0
    width: 100%
    z-index: 7
    background-color: $blue
  .header-main
    &__desc
      margin-top: 60px
      font-size: 18px
    &__title
      font-size: 32px
    &__button
      width: 210px
      height: 67px
      & span
        width: 210px
        height: 20px
        padding-top: 0
        font-size: 16px
  .timetable
    &__wrp
      gap: 5px
      justify-content: space-around
    &__dishes
      font-size: 15px
      & span
        margin: 0 8px
    &__line
      margin-top: 10px
      width: 369px
  .timetable-right
    &__square
      display: none
    &__img1
      min-width: 323px
      height: 300px
      top: 0
      left: 20px
    &__img2
      min-width: 323px
      height: 300px
      top: 363px
      left: 20px
  .rewiews
    min-height: 385px
    margin-bottom: 16px
    &__rewiews-box
      width: 768px
    & img
      height: 28px
      width: 38px
      margin-bottom: 16px
  .rewiews-box
    &__title
      font-weight: 600
      font-size: 24px
      margin-bottom: 5px
    &__desc
      font-size: 14px
      text-align: center
      margin-bottom: 10px
    &__name
      font-size: 18px

  .gallery
    margin-bottom: 320px