.button
  width: 270px
  height: 100px
  background: $blue
  transition: all 0.2s ease-in
  &:hover
    background:#354381
  &:active
    border-radius: 7px
    opacity: 70%
  & span
    display: inline-block
    width: 270px
    height: 100px
    padding-top: 40px
    font-weight: 500
    font-size: 18px
    line-height: 1.33
    color: $white
    transition: all 0.2s ease-in
    &:hover
      color: $brown
    &:active
      color: $brown
