.rewiews
  min-height: 782px
  background: $blue
  display: flex
  justify-content: center
  align-items: center
  margin-bottom: 109px
  &__rewiews-box
    width: 1200px
    display: flex
    flex-direction: column
    align-items: center
    justify-content: center
.rewiews-box
  &__icon
    height: 58px
    width: 83px
    margin-bottom: 46px
  &__title
    font-weight: 600
    font-size: 36px
    line-height: 1.11
    text-align: center
    color: $brown
    margin-bottom: 8px
  &__desc
    line-height: 2.5
    text-align: center
    color: $white
    margin-bottom: 40px
  &__name
    font-weight: 600
    font-size: 24px
    line-height: 1.67
    text-align: center
    text-transform: uppercase
    letter-spacing: 0.245em
    color: $brown


