// Секция контакты и формы
.contacts
  display: flex
  justify-content: space-between
  margin-bottom: 50px
.left-box
  max-width: 500px
  gap: 40px
  &__title
    font-weight: 700
    font-size: 48px
    line-height: 1.83
    margin-bottom: 35px
  &__wrp
    display: flex
    flex-direction: row
    align-items: center
    padding-bottom: 40px
    &:last-child
      padding-bottom: 0
  &__img
    display: flex
    align-items: center
    justify-content: center
    margin-right: 16px
    width: 64px
    height: 64px
    border: 1px solid $brown
    border-radius: 100px
    background: $brown
  &__item
    display: flex
    flex-direction: column
    & a
      &:hover
        text-decoration: underline
        color: $brown

.right-box
  &__title
    font-weight: 700
    font-size: 48px
    line-height: 1.83
    margin-bottom: 29px

// ============Последний блок==============

.footer-last
  padding-bottom: 200px
  margin-top: 40px
  display: flex
  align-items: center
  justify-content: space-between
  &__social
    display: inline-flex
  &__circle
    position: relative
    display: flex
    justify-content: center
    align-items: center
    width: 48px
    height: 48px
    border: 1px solid $black
    border-radius: 100px
    margin-right: 25px
    transition: all 0.1s ease-in
    &:last-child
      margin-right: 0px
    &:hover
      color: $white
      background: $blue
      transform: scale(1.1)
      border: 1px solid $blue
    & a:before
      content: ''
      position: absolute
      top: 0
      left: 0
      width: 100%
      height: 100%
