.timetable
  &__container
    display: flex
    justify-content: space-between
    margin-top: 345px
  &__wrp
    display: flex
    flex-direction: column
    gap: 20px
  &__title
    font-weight: 600
    font-size: 24px
    line-height: 1.33
    margin-bottom: 10px
  &__dishes
    font-size: 18px
    line-height: 1.7
    display: flex
    align-items: center
    & span
      margin: 0 12px
      display: inline-flex
      width: 5px
      height: 5px
      border-radius: 50%
      background: #D3B47F
  &__line
    margin-top: 20px
    width: 469px
    border: 1px solid #E7E7E7

// Правая часть секции
.timetable-right
  &__wrp
    width: 700px
    min-height: 638px
    position: relative
    margin-bottom: 90px
  &__square
    position: absolute
    width: 471px
    height: 502px
    left: 129px
    top: 51px
    border: 2px solid $brown
    z-index: -1
  &__img1
    max-height: 486px
    max-width: 323px
    position: absolute
    left: 34px
    top: 152px
  &__img2
    max-height: 486px
    max-width: 323px
    position: absolute
    left: 379px
    top: 0px

