.form
  max-width: 670px
  &__fieldset-name
    display: flex
    flex-wrap: wrap
    margin-bottom: 20px
  & legend
    margin-bottom: 10px
    display: block
    font-weight: 400
    font-size: 16px
    line-height: 2
  & input
    min-width: 320px
    height: 80px
    padding-left: 16px
    border: 1px solid #cccccc
    &::placeholder
      line-height: 1.5
      color: $grey
  & textarea
    padding-left: 16px
    padding-top: 28px
    min-width: 670px
    height: 125px
    border: 1px solid #CCCCCC
.input-box
  width: 320px
  margin-right: 19px
  &:last-child
    margin-right: 0
