.testimonials
  margin-bottom: 80px
  &__wrp
    display: flex
    align-items: center
    justify-content: space-between
    margin-bottom: 30px
    gap: 30px
  &__text-box
    position: relative
  &__title
    text-transform: uppercase
    font-weight: 700
    font-size: 40px
    line-height: 2.2
    margin-bottom: 20px
  &__underline
    position: absolute
    width: 108px
    height: 0px
    left: -54px
    top: 72px
    border: 2px solid $brown
  &__desc
    line-height: 2
    color: $grey

